<template>
  <div class="container">
    <div>
      <video
        ref="video"
        width="100%"
        :src="`https://1251483318.vod2.myqcloud.com${video.src}`"
        controls
      ></video>
    </div>

    <div class="comntent">
      <van-cell v-for="(item, index) in list" :key="index">
        <div>{{ index + 1 }}、{{ item.title }}</div>
        <van-button
          :type="current === index ? 'info' : 'default'"
          size="mini"
          round
          @click="handleStudy(index)"
          >学习</van-button
        >
      </van-cell>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      current: 0,
      list: [],
    };
  },
  computed: {
    video() {
      return this.list[this.current] || {};
    },
  },
  created() {
    this.getVideos();
  },
  methods: {
    getVideos() {
      this.list = require("./list.json");
    },
    handleStudy(index) {
      this.current = index;
      this.$nextTick(() => {
        this.$refs.video.play();
      });
    },
  },
};
</script>
<style lang="scss" scoped>
.container {
  height: 100vh;
  display: flex;
  flex-direction: column;
}
.van-cell__value {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.comntent {
  flex: 1;
  overflow: scroll;
}
</style>
